.main {
  --columns: 4;
  grid-template-columns: repeat(var(--columns), 200px);
  justify-content: center;
  gap: 0 30px;
  font-family: Arial, Helvetica, sans-serif;
  display: grid;
}

@media (width >= 500px) {
  .main {
    --columns: 2;
  }
}

@media (width >= 1000px) {
  .main {
    --columns: 4;
  }
}

.person {
  box-sizing: border-box;
  width: fit-content;
  padding: 10px 10px 16px;
  transition: border-radius 1s, scale 1s;
}

.person:hover {
  z-index: 1;
  background-color: #eee;
  border-radius: 10px;
  scale: 1.2;
}

.person:hover .person__photo img {
  border-radius: 60px;
}

.person:hover .person__extra {
  opacity: 1;
}

.person__photo {
  width: 200px;
  height: 200px;
  position: relative;
}

.person__photo img {
  width: inherit;
  border-radius: 50%;
  transition: all 1s;
}

.person__photo span {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  height: fit-content;
  padding: 7px 12px;
  font-weight: bold;
  display: flex;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.person__extra {
  opacity: 0;
}

.person__details {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 9px;
  margin-top: 8px;
  display: flex;
}

.person__name {
  color: #2f4f4f;
  font-size: 20px;
  font-weight: bold;
}
